import { Popover, PopoverBody } from 'reactstrap';
import classnames from 'classnames';
import './form.scss';


const Input = ({
  id,
  type,
  value,
  pop,
  popMsg,
  onBlur,
  onFocus,
  onChange,
  color,
}) => (
  <div
    className={classnames({
      input_cont: true,
      light: color === 'light',
    })}
  >
    <label htmlFor={id}></label>
    <input
      id={id}
      value={value}
      name={id}
      type={type}
      onBlur={onBlur}
      onFocus={onFocus}
      onChange={onChange}
    />
    <Popover placement="right" target={id} isOpen={pop}>
      <PopoverBody>
        {popMsg}
      </PopoverBody>
    </Popover>
  </div>
);

export default Input;
